import React from 'react'
import {useState,useEffect} from 'react';
import get from 'axios';
import 'bootstrap/dist/css/bootstrap.min.css'
import './StyleSheet.css';
import Form from 'react-bootstrap/Form'
import InputGroup from 'react-bootstrap/InputGroup'
import ReactHTMLTableToExcel from 'react-html-table-to-excel'

function Content() {
  const [info,setinfo]=useState([]);
  const[currentPage,setCurrentpage]=useState(1);
  const recordsperpage=5;
  const lastIndex=currentPage * recordsperpage;
  const firstIndex=lastIndex-recordsperpage;
  const records=info.slice(firstIndex,lastIndex);
  const npage=Math.ceil(info.length / recordsperpage);
  const numbers=[...Array(npage + 1).keys()].slice(1);
  const [search,setseacrh]=useState('');
  useEffect(function(){
      get("https://vijaya3071.github.io/apitest/information.json").then(function(output){
      //my own api deployed in git hub and used here 
      //logic to set song array with the api data
       setinfo(output.data.information)
      console.log(output.data.information)
      
    }).catch(function(error)
    {
      //Logic to catch for any errors
     
      console.log(error)
    })
  },[])//added empty dependency array for getting rid of your infinite loop
  return (
    <div className='container'>
      <div className='mt-3'>
        <h3>Candidate Information</h3>
        <Form>
          <InputGroup className='my-3'>
            <Form.Control 
            onChange={(e) => setseacrh(e.target.value)} placeholder='Search By Name'/>
          </InputGroup>
        </Form>
        <ReactHTMLTableToExcel
                    id="test-table-xls-button"
                    className="download-table-xls-button btn btn-success mb-3"
                    table="table-to-xls"
                    filename="tablexls"
                    sheet="tablexls"
                    buttonText="Export Data to Excel Sheet"/>
   <table className='table' id='table-to-xls'>
    <thead>
      <tr>
      <th>Name</th>
      <th>Phone</th>
      <th>email</th>
      </tr>
    </thead>
    <tbody>
      {
        records.filter((user) =>{
          return search.toLowerCase() ==='' ? user:user.Name.toLowerCase().includes(search);
        }).map((user,index)=>
        {
          return(

           <tr key={index}>
           <td>{user.Name}</td>
           <td>{user.Phone}</td>
           <td>{user.Email}</td>
           </tr>
          )
        })
      }
    </tbody>
   </table>
<nav>
    <ul className="pagination">
        <li className="page-item">
          <a href='#' className='page-link' 
          onClick={prePage}>Prev</a>
        </li>
      {
        numbers.map((n,i) =>(
          <li className={`page-item ${currentPage === n ?'active' : ''}`} key={i}>
             <a href='#' className='page-item'
             onClick={()=>changeCPage(n)}>{n}</a>
           </li>
        ))
      }
      <li className="page-item">
          <a href='#' className='page-link' 
          onClick={nextPage}>Next</a>
        </li>
</ul></nav>
      </div>
      
    </div>
  )

  function prePage(){
    if(currentPage !==firstIndex){
      setCurrentpage(currentPage-1)
    }
    
  }
  function nextPage(){
if(currentPage !== lastIndex)
{
  setCurrentpage(currentPage+1)
}
  }
  function changeCPage(id){
setCurrentpage(id)
  }
}

export default Content